<template>
  <section class="WelcomeHero">
    <div ref="radial" class="WelcomeHero-radialPulse" :style="radialAnimation"></div>
    <div class="WelcomeHero-bg" />
    <svg :style="radialAnimation" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="100%" height="100%">
      <clipPath :style="radialAnimation" id="clip">
        <path id="donut"
          d="M 707.86204,188.53974 A 117.70758,117.70758 0 0 1 590.15447,306.24731 117.70758,117.70758 0 0 1 472.4469,188.53974 117.70758,117.70758 0 0 1 590.15447,70.832159 117.70758,117.70758 0 0 1 707.86204,188.53974 Z M 592.71291,43.538429 A 142.44322,142.44322 0 0 0 450.26917,185.98217 142.44322,142.44322 0 0 0 592.71291,328.42319 142.44322,142.44322 0 0 0 735.15664,185.98217 142.44322,142.44322 0 0 0 592.71291,43.538429 Z"
          clip-rule='evenodd'/>
      </clipPath>
      <filter id="blur">
        <feTurbulence type="fractalNoise" baseFrequency="0.1 0.1" numOctaves="1" result="warp"></feTurbulence>
        <feDisplacementMap xChannelSelector="R" yChannelSelector="G" scale="1" in="SourceGraphic" in2="warp" result="map"/>

        <feGaussianBlur in="map" stdDeviation="0" result="blurred" />

      </filter>
    </svg>

    <audio ref="clickSound" preload="true" src="static/sounds/click.mp3"></audio>

    <div class="WelcomeHero-wrapper u-marginTop--small">
      <div class="WelcomeHero-textTitle">
        Get Rhythm stats for your music
      </div>

      <div class="WelcomeHero-uploadZone u-marginTop--medium">
        <div class="WelcomeHero-uploadForm">
          <label class="Button">
            <input type="file" class="WelcomeHero-uploadHidden" @change="fileChanged"
              :accept="acceptedFormats"/>
            <icon name="upload"></icon>
            <span class="u-marginLeft--xSmall">Upload</span>
          </label>

          <aside>
            <div class="WelcomeHero-extensionTitle u-marginTop--tiny">
              Supported extensions:
            </div>
            <div ref="extList" class="WelcomeHero-extensionList u-marginTop--tiny">MP3, WAV, OGG, AAC</div>
          </aside>
        </div>

        <div class="WelcomeHero-textTitle">
          Or
        </div>

        <div class="WelcomeHero-recordForm u-marginLeft--medium">
          <button class="Button">
            <icon name="microphone"></icon>
            <span class="u-marginLeft--xSmall">Record</span>
          </button>
        </div>

      </div>


    </div>

  </section>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'

  const audioTypes = 'audio/mp3,audio/ogg,audio/wav,audio/aac'

  export default {
    data () {
      return {
        'appLoaded': false
      }
    },
    computed: {
      ...mapGetters({
        bpmClickMuted: 'getBpmClick'
      }),
      radialAnimation () {
        return {
          animationDuration: this.getBpm(),
          animationPlayState: this.appLoaded ? 'running' : 'paused'
        }
      },
      acceptedFormats () {
        return audioTypes
      }
    },
    methods: {
      ...mapGetters({
        getBpm: 'getBpmRadial',
        isMusicLoaded: 'isMusicLoaded'
      }),
      ...mapActions({
        setMusicLoaded: 'setMusicLoaded'
      }),
      playClick () {
        this.$refs.clickSound.currentTime = 0
        this.$refs.clickSound.play()
      },
      fileChanged (ev) {
        let file = ev.path[0].files[0]

        if (!audioTypes.includes(file.type)) {
          // Flash red extensions
          this.$refs.extList.classList.add('WelcomeHero-extensionList--alert')
          setTimeout(() => {
            this.$refs.extList.classList.remove('WelcomeHero-extensionList--alert')
          }, 1000)
        } else {
          this.setMusicLoaded(true)
          this.$parent.$emit('fileLoaded')
        }
      }
    },
    watch: {
      bpmClickMuted (val) {
        if (!val) {
          this.$refs.radial.addEventListener('animationiteration', this.playClick)
        } else this.$refs.radial.removeEventListener('animationiteration', this.playClick)
      }
    },
    mounted () {
      this.$on('appLoaded', () => {
        this.appLoaded = true
      })
    }
  }
</script>

<style lang="scss">
</style>



